.About {
  display: flex;
  flex-flow: column;
  align-items: center;
  color: var(--color-primary-text);
  margin: 10rem 1rem 0 1rem;
}

.About__title {
  font-size: 2.5rem;
  letter-spacing: 3px;
  font-weight: bold;
  margin-bottom: 1rem;
}

.About h2,
p {
  font-size: 1.2rem;
  text-align: start;
  font-weight: normal;
  max-width: 600px;
}

.About p {
  margin-bottom: 0.5rem;
}

.About__icons {
  display: flex;
  gap: 5rem;
  margin-top: 5rem;
}

.icon {
  width: 40px;
}
